import React, { Component } from 'react';

class TodoItem extends Component {
  render() {
    const { text, isCompleted } = this.props.item;
    return (
      <div>
        <input
          type="checkbox"
          onChange={() => this.props.onToggle(this.props.item)}
          checked={isCompleted}
        />
        <label>{text}</label>
      </div>
    );
  }
}

export default TodoItem;



