import React, { Component } from 'react';
import PouchDB from 'pouchdb';
import TodoList from './TodoList';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.createTodosDB();

    this.state = {
      todos: [],
      text: '',
      showOnlyCompleted: false,
    };

    this.db
      .changes({
        since: 'now',
        live: true,
      })
      .on('change', () => this.fetchTodos());
  }

  componentDidMount() {
    this.fetchTodos();
  }

  fetchTodos() {
    this.getAllTodos().then(todos => this.setState({ todos }));
  }

  createTodosDB() {
    this.db = new PouchDB('todos');
  }

  addTodo(text) {
    var todo = {
      _id: new Date().toISOString(),
      text,
      isCompleted: false,
    };
    this.db
      .put(todo)
      .then(response => {
        console.log(`Successfully posted ${text}`);
      })
      .catch(error => {
        console.log(error);
      });
  }

  getAllTodos() {
    return new Promise((resolve, reject) => {
      this.db
        .allDocs({ include_docs: true, descending: true })
        .then(result => {
          resolve(result.rows.map(item => item.doc));
        })
        .catch(error => reject(error));
    });
  }

  clearInput() {
    this.setState({ text: '' });
  }

  onSubmit(event) {
    event.preventDefault();
    const text = this.state.text.trim();
    if (text.length) this.addTodo(text);
    this.clearInput();
  }

  onChange(event) {
    this.setState({ text: event.target.value });
  }

  onToggle(item) {
    item.isCompleted = !item.isCompleted;
    this.db.put(item);
  }

  onShowOnlyCompletedToggle() {
    this.setState({ showOnlyCompleted: !this.state.showOnlyCompleted });
  }

  deleteCompleted() {
    Promise.all(
      this.state.todos.map(
        item => (item.isCompleted ? this.db.remove(item) : Promise.resolve()),
      ),
    )
      .then(() => this.fetchTodos())
      .catch(error => console.log(error));
  }

  render() {
    return (
      <div className="App">
        <form onSubmit={this.onSubmit.bind(this)} noValidate>
          <input
            autoComplete="off"
            autoFocus
            placeholder="What needs to be done?"
            type="text"
            onChange={this.onChange.bind(this)}
            value={this.state.text}
          />
        </form>
        <button onClick={this.deleteCompleted.bind(this)}>
          Delete Completed
        </button>
        <input
          type="checkbox"
          onChange={() => this.onShowOnlyCompletedToggle()}
          checked={this.state.showOnlyCompleted}
        />
        <label>show only completed</label>
        <TodoList
          list={this.state.todos}
          onToggle={this.onToggle.bind(this)}
          showOnlyCompleted={this.state.showOnlyCompleted}
        />
      </div>
    );
  }
}

export default App;



